jQuery(function ($) {
  // ######  ##      ## #### ########  ######## ########
  // ##    ## ##  ##  ##  ##  ##     ## ##       ##     ##
  // ##       ##  ##  ##  ##  ##     ## ##       ##     ##
  //  ######  ##  ##  ##  ##  ########  ######   ########
  //       ## ##  ##  ##  ##  ##        ##       ##   ##
  // ##    ## ##  ##  ##  ##  ##        ##       ##    ##
  //  ######   ###  ###  #### ##        ######## ##     ##
  if ($('.MV-swiper__container')[0]) {
    // Swiper
    const mvSwiper = new Swiper('.MV-swiper__container', {
      effect: 'cube', // slide,fade,cube,coverflow
      speed: 1500, // 
      loop: true, // 
      loopAdditionalSlides: 5, //
      centeredSlides: true,
      slidesPerView: 1, //
      autoplay: {
        delay: 5000, // 
        disableOnInteraction: false // 
      },
      navigation: {
        nextEl: '.MV-swiper-button-next',
        prevEl: '.MV-swiper-button-prev'
      },
      pagination: {
        el: '.MV-swiper-pagination',
        // clickable: true
        type: 'fraction'
      }
    })
  }
  // ######   #######   #######  ##    ## #### ########
  // ##    ## ##     ## ##     ## ##   ##   ##  ##
  // ##       ##     ## ##     ## ##  ##    ##  ##
  // ##       ##     ## ##     ## #####     ##  ######
  // ##       ##     ## ##     ## ##  ##    ##  ##
  // ##    ## ##     ## ##     ## ##   ##   ##  ##
  //  ######   #######   #######  ##    ## #### ########
  /* global Cookies */
  // Cookies.remove('opFlag')
  const opFlag = Cookies.get('opFlag')
  if (opFlag !== 1) {
    Cookies.set('opFlag', 1, { expires: 1 / 24, path: '/' }) //Cookie
  }
  // ######## ########  ##     ## ##    ## ##    ##  #######
  //    ##    ##     ## ##     ## ###   ## ##   ##  ##     ##
  //    ##    ##     ## ##     ## ####  ## ##  ##   ##     ##
  //    ##    ########  ##     ## ## ## ## #####     #######
  //    ##    ##   ##   ##     ## ##  #### ##  ##   ##     ##
  //    ##    ##    ##  ##     ## ##   ### ##   ##  ##     ##
  //    ##    ##     ##  #######  ##    ## ##    ##  #######
  function trunk8() {
    $('.js-t8.line4').trunk8({
      // 
      lines: 4,
      fill: '...'
    })
    $('.js-t8.line3').trunk8({
      // 
      lines: 3,
      fill: '...'
    })
    $('.js-t8.line2').trunk8({
      // 
      lines: 2,
      fill: '...'
    })
    $('.js-t8.line1').trunk8({
      // 
      lines: 1,
      fill: '...'
    })
  }
  if ($('.js-t8')[0]) {
    // 
    trunk8()
    $(window).on('load resize ', function () {
      trunk8()
    })
  }
})
